/* =========================================
   Characteristics — 25% | 25% | 50%
   Consistent, elegant: headers inside the card grid.
   Body/Mind: name | value
   Spirit:    name-L | value-L | gap | value-R | name-R
   ========================================= */
@card-gap: 12px;
@col-gap: 8px;
@row-gap: 6px;
@center-gap: 10px;

.fs2e.sheet.actor.character {
  section.characteristics {
    display: grid;
    grid-template-columns: minmax(0, 1fr) minmax(0, 1fr) minmax(0, 2fr); // 25/25/50
    gap: @card-gap;
    align-items: start;

    /* ----- Card base (applies to Body, Mind, Spirit) ----- */
    > .attribute-group {
      padding: 8px;
      border: 1px solid #3a3a3a;
      border-radius: 8px;
      background: rgba(0, 0, 0, 0.05);
      min-width: 0;

      /* Card-level grid: header + rows */
      display: grid;
      grid-template-columns: 1fr auto;  /* name | value (default) */
      column-gap: @col-gap;
      row-gap: @row-gap;

      > h3 {
        grid-column: 1 / -1;   /* header spans full width */
        margin: 0 0 @row-gap 0;
      }

      /* Rows contribute cells directly to the card grid */
      .attribute-grid { display: contents; }
      .attribute-grid > label.stat { display: contents; }

      /* Default placement (Body/Mind) */
      .attribute-grid .name  { grid-column: 1; justify-self: start; text-align: left;  min-width: 0; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }
      .attribute-grid .value { grid-column: 2; justify-self: end;   text-align: right; min-width: 3ch; }
    }

    /* ----- Spirit overrides: mirrored 5-track layout ----- */
    > .attribute-group:has(.attribute-grid__spirit-left) {
      grid-template-columns:
        minmax(0, 1fr)  /* name-L   */
        auto            /* value-L  */
        @center-gap     /* gap      */
        auto            /* value-R  */
        minmax(0, 1fr); /* name-R   */
      column-gap: @col-gap;
      row-gap: @row-gap;

      /* Keep lists/rows flattened from base rules */
      /* Left side: labels left, numbers toward center */
      .attribute-grid__spirit-left  .name  { grid-column: 1; justify-self: start;  text-align: left;  }
      .attribute-grid__spirit-left  .value { grid-column: 2; justify-self: end;    text-align: right; }

      /* Right side: numbers toward center, labels flush-right */
      .attribute-grid__spirit-right .value { grid-column: 4; justify-self: start;  text-align: left;  }
      .attribute-grid__spirit-right .name  { grid-column: 5; justify-self: end;    text-align: right; }

      /* Align rows across left/right (3 rows) */
      .attribute-grid__spirit-left  > label.stat:nth-child(1) .name,
      .attribute-grid__spirit-left  > label.stat:nth-child(1) .value { grid-row: 2; }
      .attribute-grid__spirit-left  > label.stat:nth-child(2) .name,
      .attribute-grid__spirit-left  > label.stat:nth-child(2) .value { grid-row: 3; }
      .attribute-grid__spirit-left  > label.stat:nth-child(3) .name,
      .attribute-grid__spirit-left  > label.stat:nth-child(3) .value { grid-row: 4; }

      .attribute-grid__spirit-right > label.stat:nth-child(1) .value,
      .attribute-grid__spirit-right > label.stat:nth-child(1) .name  { grid-row: 2; }
      .attribute-grid__spirit-right > label.stat:nth-child(2) .value,
      .attribute-grid__spirit-right > label.stat:nth-child(2) .name  { grid-row: 3; }
      .attribute-grid__spirit-right > label.stat:nth-child(3) .value,
      .attribute-grid__spirit-right > label.stat:nth-child(3) .name  { grid-row: 4; }
    }
  }
}

/* Responsive collapse */
@media (max-width: 1100px) {
  .fs2e.sheet.actor.character section.characteristics {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
@media (max-width: 700px) {
  .fs2e.sheet.actor.character section.characteristics {
    grid-template-columns: 1fr;
  }
}
/* Ensure FS2E tabs show their content regardless of core styles */
.window-app .fs2e.sheet.actor .sheet-body > .tab {
  display: none !important;
  visibility: hidden !important;
}
.window-app .fs2e.sheet.actor .sheet-body > .tab.active {
  display: block !important;
  visibility: visible !important;
}

/* =========================================
   Skills — two cards stacked, each 3 columns
   Column-first flow: top→bottom, then next column
   Matches Characteristics card styling.
   ========================================= */
.fs2e.sheet.actor.character {
  .tab.skills section.skills {
    display: grid;
    grid-template-columns: 1fr;   // one card per row
    gap: @card-gap;
    align-items: start;
  }

  /* Card base for each section (Natural, Learned) */
  .tab.skills section.skills > .attribute-group {
    padding: 8px;
    border: 1px solid #3a3a3a;
    border-radius: 8px;
    background: rgba(0, 0, 0, 0.05);
    min-width: 0;

    > h3 {
      margin: 0 0 @row-gap 0;
      font-weight: 600;
    }
  }

  /* Three-column, column-first list */
  .tab.skills .skills-grid {
    column-count: 3;
    column-gap: @col-gap;
  }

  /* Each skill row: name | value */
  .tab.skills .skills-grid label.stat {
    break-inside: avoid;
    display: grid;
    grid-template-columns: 1fr auto; /* name | value */
    column-gap: @col-gap;
    padding: 2px 0;
  }

  .tab.skills .skills-grid .name {
    min-width: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    justify-self: start;
    text-align: left;
  }

  .tab.skills .skills-grid .value {
    min-width: 3ch;
    justify-self: end;
    text-align: right;
  }

  /* Responsive columns */
  @media (max-width: 1100px) {
    .tab.skills .skills-grid { column-count: 2; }
  }
  @media (max-width: 700px) {
    .tab.skills .skills-grid { column-count: 1; }
  }
}
